.bt-table{
    background-size: 30px ;
    background-repeat: no-repeat;
    background-position: center;
    height: 40px;
    width: 40px;
}

.bt-delete{
    background-image: url(/static/media/trash.f4a10743.png);
    background-color: #b52e3b;
    color: white;
}

.bt-edit{
    background-image: url(/static/media/edit.3f326698.png);
