/* Absolute Center Spinner */
.loading {
    white-space: nowrap;
    background-color: white;
}

.loading:hover{
    -webkit-animation: none;
            animation: none;
}

.load-image {
    display: block;
    border-radius: 15px;
    margin-left: auto;
    margin-right: auto;
    max-width: 100%;
}
.SearchBox {
    min-width: 55%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-bottom: 0;
    display: inline-flex;
    align-content: center;
    justify-content: center;
}

.SearchBox-input {
    width: calc(99% - 45px) !important;
    height: 40px;

}

.bottomline {
    justify-content: space-around;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding-right: 5px;
    padding-left: 5px;

}

.SearchIcon {
    background-image: url(data:image/png;base64,);
    background-size: 30px ;
    background-repeat: no-repeat;
    background-position: center;
    height: 40px;
    width: 45px !important;
}
.custom-select {
    margin-left: 5px;
    margin-right: 5px;
    max-width: 20%;
}

.button-add {
    position: relative;
    left: 50px;
}

.content-info {
    margin-top: 10px;
    margin-bottom: 10px;
    margin-left: 10px;
    width: 100%;
}

.MainTitle {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}

.headerItem {
    margin-bottom: 15px;
    height: 40px;
}
.navbar {
    width: 100%;
}


.page-item.active ._pagination-active {
    background-color: #21252a;
    border-color: #21252a;
}

.page-item ._page-link {
    color:#21252a;
    max-width: 100%;
}

.page-item ._page-link:focus {
    box-shadow:none;
}

.arrows {
    height: 18px;
    width: 18px;
}

.footer {
    display: flex;
    align-content: center;
    justify-content: center;
    max-width: 100vw;
    margin: 0 auto;
}

.Site {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
}
.Site-content {
    flex: 1 0 auto;
    padding: var( — space) var( — space) 0;
    width: 100%;
}
.Site-content:after {
    content: ‘\00a0’;
    display: block;
    margin-top: var( — space);
    height: 0;
    visibility: hidden;
}

.page-footer {
    /* position: relative;
    left: 0;
    bottom: 0; */
    width: 100%;
    background-color: #21252a;
    /* transform: translateY(); */
}

.footer-copyright {
    color: aliceblue;
}

a, a:hover{
    color: #4597a5
}
.alinha{
    text-align: center;
}

.sortHead {
    background-image: url(data:image/png;base64,);
    background-size: 20px;
    background-repeat: no-repeat;
    background-position: 95% center;
}

.headerCommon {
    width: 10%;
}

.table {
    table-layout: fixed;    
    display: block;
    background-color: white;
    padding: 0;
    border: 0;
}

.customer {
    width: 50%;
}

.city {
    width: 100%;
}
.alert{
    margin-top: 20px;
}

label{
    width: 100%;
}

.modal-label {
    margin-bottom: 0;
    margin-top: 10px;
}
.welcome {
    -webkit-transition: 0.3s;
    transition: 0.3s;
    font-size: 10vw;
    font-weight: bolder;
    color: dimgrey;
    text-shadow: darkgray 0 0 0.3em;
    width: 100%;
    display: flex;
    position: relative;
    margin: 15vh 0 0 0;
    align-content: center;
    align-items: center;
    justify-content: center;
    height: 11vh;
}

body {
    background-color: whitesmoke;
}

